import React from 'react';
import { Card, Avatar, Row, Button, message } from 'antd';
import { DeleteTwoTone, PlusCircleOutlined, EyeTwoTone } from '@ant-design/icons';
import axios from 'axios';
import { connect } from 'react-redux';

import { campaign_banner, campaign_logo } from 'api';


const Campaign = (props) => {
    let { history, user_id } = props;

    let [campaigns, setCampaigns] = React.useState([]);

    const getCampaigns = async () => {
        try {
            let res = await axios.post('/campaignlist', { user_id });
            setCampaigns(res.data.data);
            message.success(res.data.message);
        } catch (error) {
            error.response ?
                message.error(error.response.data)
                :
                message.error(error.message);
        }
    }


    React.useEffect(() => {
        getCampaigns();
    }, []);

    return (
        <Card style={styles.container}>
            <Row justify='end'>
                <Button
                    type="primary"
                    icon={<PlusCircleOutlined />}
                    onClick={() => history.push('/campaign/form')}
                >
                    Add Campaign
                </Button>
            </Row>
            <Row justify='space-around' className="mt-2">
                {
                    campaigns.map(item => {
                        return (
                            <Card
                                key={item.campaign_id}
                                hoverable
                                style={{ width: 320, boxShadow: '0 2px 8px rgba(0, 0, 0, 0.2)' }}
                                onClick={() => history.push(`/campaign/details/${item.campaign_id}`)}
                                cover={
                                    <img
                                        alt="banner"
                                        src={campaign_banner + item.campaign_banner}
                                        height={200}
                                    />
                                }
                                actions={[
                                    <EyeTwoTone key="View" />,
                                    <DeleteTwoTone key="delete" twoToneColor='#ff6e6e' />
                                ]}
                            >
                                <Card.Meta
                                    avatar={<Avatar src={campaign_logo + item.campaign_logo} />}
                                    title={item.campaign_campaignname}
                                    description={item.campaign_website}
                                />
                            </Card>
                        )
                    })
                }
            </Row>
        </Card>
    )
}

const styles = {
}

const mapStateToProps = state => ({
    user_id: state.auth.user.user_id
});

export default connect(mapStateToProps)(Campaign);
